import config from './config.js';
import login from './login';
import Axios from '../../node_modules/axios';

const Util = {

  shortthmonth: [{
    '01': '..',
    '02': '..',
    '03': '..',
    '04': '.',
    '05': '..',
    '06': '..',
    '07': '..',
    '08': '..',
    '09': '..',
    '10': '..',
    '11': '..',
    '12': '..',
  }],
      
  fullthmonth: [
    { 'code': '01', 'name': '' },
    { 'code': '02', 'name': '' },
    { 'code': '03', 'name': '' },
    { 'code': '04', 'name': '' },
    { 'code': '05', 'name': '' },
    { 'code': '06', 'name': '' },
    { 'code': '07', 'name': '' },
    { 'code': '08', 'name': '' },
    { 'code': '09', 'name': '' },
    { 'code': '10', 'name': '' },
    { 'code': '11', 'name': '' },
    { 'code': '12', 'name': '' }
  ],
  getdThai (d)
  {
    let now = new Date(); 
    let thday = ['', '',
      '', '', '', '', '']; 
    let thmonth = ['', '', '',
      '', '', '', '', '', '',
      '', '', ''];
    return thday[d];
  },
  getmThai (m)
  {
    let now = new Date(); 
    let thday = ['', '',
      '', '', '', '', '']; 
    let thmonth = ['', '', '',
      '', '', '', '', '', '',
      '', '', ''];
    return thmonth[m];
  },
  getMonthByCode (code) {
    return this.fullthmonth.filter(
      function (data) { return data.code === code }
    );
  },
  getUnit () {
    const requestOptions = {
      method: 'GET',
      headers: login.getAuthHeader()
    };
    
    return Axios.get(`${config.API_URL}util/unit/all`, requestOptions).then(this.handleResponse);
  },
};
export default Util;


